import React, { FC, useEffect, useState } from "react";
import {Link} from "gatsby"
import { InternalLinks } from "../../../utils/links";
import BulbIcon from "../Icons/BulbIcon/BulbIcon";
import SuccessFormSubmitMessage from "../SuccessFormSubmitMessage/SuccessFormSubmitMessage";
import {
  Description,
  Header,
  Icon,
  StyledSubscribeBlock,
  SubscribeForm,
  Title,
  Notification,
  SubsribeButton,
} from "./styled";
import Cookies from "js-cookie"

interface Props {
  title: string;
  description: string;
}

const SubscribeBlock: FC<Props> = ({ title, description }) => {
  const [isSubmitted, setIsSubmitted] = useState<boolean>(false);
  const [error, setError] = useState<boolean>(false);

  const [email, setEmail] = useState<string>("");

  useEffect(() => {
    if (error) setTimeout(() => setError(false), 3000);
  }, [error]);

  const onSubmit = () => {
    const data = {
      fields: [
        {
          objectTypeId: "0-1",
          name: "email",
          value: email,
        },
      ],
      context: {
        hutk: Cookies.get("hubspotutk"),
        pageUri: document.location.href,
        pageName: document.title,
      },
    };

    fetch(
      "https://api.hsforms.com/submissions/v3/integration/submit/21630623/9e11a6c7-d24f-4820-9508-06489e3b552b",
      {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(data),
      }
    )
      .then(() => setIsSubmitted(true))
      .catch(() => setError(true));
  };

  return (
    <StyledSubscribeBlock>
      <Header>
        <Title>{title}</Title>
        <Icon>
          <BulbIcon />
        </Icon>
      </Header>
      <Description>{description}</Description>
      <Notification>
        By clicking the subscribe button below, I hereby agree to and accept
        Telgorithmâ€™s <Link to={InternalLinks.TermsAndConditions}>terms and conditions</Link>.
      </Notification>
      {isSubmitted ? (
        <SuccessFormSubmitMessage></SuccessFormSubmitMessage>
      ) : error ? <div>Something Went Wrong, Please Try Again</div> : (
        <SubscribeForm onSubmit={(e) => {
          e.preventDefault();
          onSubmit()
        }} id="9e11a6c7-d24f-4820-9508-06489e3b552b">
          <input type="email" placeholder="Email address" name="email" value={email} 
          onChange={(e) => setEmail(e.currentTarget.value)}></input>
          <SubsribeButton type="submit" form="9e11a6c7-d24f-4820-9508-06489e3b552b">Subscribe</SubsribeButton>
        </SubscribeForm>
      )}
    </StyledSubscribeBlock>
  );
};

export default SubscribeBlock;
