import { graphql, PageProps } from "gatsby";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React, { FC } from "react";
import { Helmet } from "react-helmet";
import { ArticleHeader, LatestNewsBlock } from "../components/article";
import {
  ArticleContentContainer,
  StyledArticleContent,
} from "../components/article/styled";
import { Table } from "../components/common";
import Layout from "../components/common/Layout/Layout";
import SubscribeBlock from "../components/common/SubscribeBlock";
import TableComponent from "../components/common/Table";
import { mapArticleNodeToArticlePreviewInfo } from "../mappers";
import {MDXProvider} from '@mdx-js/react'

const components = {
  table: (props : {children: React.ReactElement}) => (
    <TableComponent.Container>
      <TableComponent.Wrapper>
        <Table>{props.children}</Table>
      </TableComponent.Wrapper>
    </TableComponent.Container>
  ),
  thead: TableComponent.Header,
  tbody: TableComponent.Body,
  tr: TableComponent.Row,
  th: TableComponent.HeaderCell,
  td: TableComponent.BodyCell,
};

const ArticlePage = ({ data }: PageProps<Queries.ArticlePageQuery>) => {
  return (
    <>
      <Helmet title={`Telgorithm | ${data.mdx?.frontmatter?.title}`} />
      <Layout>
        <ArticleContentContainer>
          <ArticleHeader
            articleType={data.mdx?.frontmatter?.articleType as string}
            date={data.mdx?.frontmatter?.date as string}
          ></ArticleHeader>
          <StyledArticleContent>
            <MDXProvider components={components}>
            <MDXRenderer
              children={data?.mdx?.body ?? "No content"}
            />
            </MDXProvider>
          </StyledArticleContent>
          <LatestNewsBlock
            latestNews={data.allMdx.edges.map((edge) =>
              mapArticleNodeToArticlePreviewInfo(edge.node)
            )}
          ></LatestNewsBlock>
          <SubscribeBlock
            title={"Subscribe to our newsletter."}
            description={
              "Navigate the cloud communications world with knowledge. Subscribe to our blog newsletter to receive the latest industry news, tips, and guidance."
            }
          />
        </ArticleContentContainer>
      </Layout>
    </>
  );
};

export default ArticlePage;

export const query = graphql`
  query ArticlePage($id: String) {
    mdx(id: { eq: $id }) {
      frontmatter {
        title
        date(formatString: "MMMM DD, YYYY")
        articleType
      }
      body
    }
    allMdx(
      filter: { fileAbsolutePath: { regex: "/news/" } }
      sort: { fields: frontmatter___date, order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            articleType
            date(formatString: "MMMM DD, YYYY")
            title
            previewImage {
              childrenImageSharp {
                gatsbyImageData(layout: FIXED)
              }
            }
          }
          id
          slug
        }
      }
    }
  }
`;
