import { Link } from "gatsby";
import React, { Children, FC, PropsWithChildren } from "react";
import { colors } from "../../../utils/colors";
import RightLongArrow from "../Icons/Arrows/RightLongArrow";
import {
  ArticleCardBody,
  ArticleCardFooter,
  ArticleCardImage,
  ArticleCardTitle,
  ArticleCardType,
  StyledArticleCardItem,
  StyledArticleCardItemLarge,
  StyledArticleCardItemMedium,
  StyledArticleCardItemSmall,
} from "./styled";
import {
  GatsbyImage,
  getImage,
  IGatsbyImageData,
  StaticImage,
} from "gatsby-plugin-image";
import { ArticlePreviewInfo } from "../../../types";

export enum ArticleType {
  PressRelease = "Press release",
  Update = "Update",
  Featured = "Featured",
}

export enum CardSize {
  S = "S",
  M = "M",
  L = "L",
}

type ArticleCardProps = {
  article: ArticlePreviewInfo;
  cardSize?: CardSize;
};

const ArticleCardItemRenderByCardSize = ({
  cardSize,
  children,
}: PropsWithChildren<{ cardSize?: CardSize }>) => {
  switch (cardSize) {
    case CardSize.L:
      return (
        <StyledArticleCardItemLarge>{children}</StyledArticleCardItemLarge>
      );
    case CardSize.M:
      return (
        <StyledArticleCardItemMedium>{children}</StyledArticleCardItemMedium>
      );
    case CardSize.L:
      return (
        <StyledArticleCardItemSmall>{children}</StyledArticleCardItemSmall>
      );
    default:
      return (
        <StyledArticleCardItemSmall>{children}</StyledArticleCardItemSmall>
      );
  }
};

const ArticleCardItem: FC<ArticleCardProps> = ({ article, cardSize }) => {
  const { date, type, title, url, previewImage } = article;
  return (
    <ArticleCardItemRenderByCardSize cardSize={cardSize}>
      <ArticleCardImage>
        <Link to={url}>
          <GatsbyImage
            image={
              previewImage?.childrenImageSharp?.[0]
                ?.gatsbyImageData as IGatsbyImageData
            }
            alt=""
          />
        </Link>
      </ArticleCardImage>
      <ArticleCardBody to={url}>
        <div>
          <ArticleCardType>{type}</ArticleCardType>
          <ArticleCardTitle>{title}</ArticleCardTitle>
        </div>
        <ArticleCardFooter>
          <div>{date}</div>
          <div>
            <RightLongArrow color={colors.primary.blue} />
          </div>
        </ArticleCardFooter>
      </ArticleCardBody>
    </ArticleCardItemRenderByCardSize>
  );
};

export default ArticleCardItem;
