import { Link } from "gatsby";
import React, { FC } from "react";
import { ArticlePreviewInfo } from "../../types";
import { Divider, Text } from "../common";
import ArticleCardItem, {
   CardSize,
} from "../common/ArticleCardItem/ArticleCardItem";
import LinkWithArrow from "../common/LinkWithArrow/LinkWithArrow";
import {
  ArticleMetaInformation,
  NewsItems,
  ReturnLink,
  StyledArticleHeader,
  StyledLatestNewsBlock,
} from "./styled";

interface ArticleHeaderProps {
  articleType: string;
  date: string;
}

interface LatestNewsBlockProps {
  latestNews: ArticlePreviewInfo[];
}

export const ArticleHeader: FC<ArticleHeaderProps> = ({
  articleType,
  date,
}) => {
  return (
    <StyledArticleHeader>
      <ReturnLink>
        <Link to={"/news"}>Return to Latest News</Link>
      </ReturnLink>
      <Divider />
      <ArticleMetaInformation>
        <p>{articleType}</p>
        <p>{date}</p>
      </ArticleMetaInformation>
    </StyledArticleHeader>
  );
};

export const LatestNewsBlock: FC<LatestNewsBlockProps> = ({ latestNews }) => {
  return (
    <StyledLatestNewsBlock>
      <Divider />
      <Text.Title.Bold.md>Latest news</Text.Title.Bold.md>
      <NewsItems>
        {latestNews.slice(0,3).map((articleItem) => (
          <ArticleCardItem article={articleItem} key={articleItem.articleId} cardSize={CardSize.S}/>
        ))}
      </NewsItems>
      <div>
        <LinkWithArrow to={"/news"}>Browse all articles</LinkWithArrow>
      </div>
      <Divider />
    </StyledLatestNewsBlock>
  );
};
