import { GatsbyLinkProps, Link } from "gatsby";
import React, { FC } from "react";
import styled from "styled-components";
import { colors } from "../../../utils/colors";
import RightArrow from "../Icons/Arrows/RightArrow";

type CustomGatsbyLinkProps = Omit<GatsbyLinkProps<{}>, "ref"> & {
  color?: string;
  disabled?: boolean;
};

const StyledLinkWithArrow = styled(Link)<CustomGatsbyLinkProps>`
  font-size: 18px;
  font-weight: 500;
  color: ${(props) => props.color ?? colors.primary.blue};
  display: flex;
  align-items: center;
  filter: ${(props) => props.disabled ? "grayscale()" : "none"};
  &:hover {
    color: ${(props) => props.disabled ? props.color ?? colors.primary.blue : "none"};
  }
`;

const LinkWithArrow: FC<CustomGatsbyLinkProps> = (props) => {
  return (
    <StyledLinkWithArrow {...props}>
      {props.children}
      <RightArrow color={props.color ?? colors.primary.blue }/>
    </StyledLinkWithArrow>
  );
};

export default LinkWithArrow;
